.NoteInput {
    max-width: 1200px;
    margin-top: 1em;
    -ms-flex-item-align: center;
        align-self: center;
}

.NoteInput > input {
    width: 12em;
    border-right: none;
}

.NoteInput > button {
    margin: 0;
}

@media (max-width: 600px) {
    .NoteInput > input {
        width: 8em;
    }
}



